.services {
  background-color: $bg-services;
  position: relative;
}

.services__container {
  margin: 0 auto;
  padding: 70px 0 0 0;
  max-width: 1150px;
  min-height: 490px;
  background-image: url('../img/content/hero-mobile@1x.jpg');
  background-position: 50% -18px;
  background-size: 575px 373px;
  background-repeat: no-repeat;
  box-sizing: border-box;
}

@media (min-resolution: 144dpi),
       (min-resolution: 1.5dppx) {
  .services__container {
    background-image: url('../img/content/hero-mobile@2x.jpg');
  }
}

.services__header {
  margin: 0 auto 122px auto;
  width: 260px;
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  text-align: center;
}

.services__item-btn {
  padding: 0 36px 0 27px;
  height: 103px;
  display: flex;
  justify-content: space-between;
  border: none;
  outline: none;
  cursor: pointer;

  &--light {
    background-color: $light-green;
  }

  &--dark {
    padding-right: 27px;
    background-color: $dark-green;
  }

  &:hover {

    .services__item-icon {
      opacity: 0.3;
    }
  }

  &:active {

    .services__item-icon {
      opacity: 0.07;
    }

    .services__item-text {
      opacity: 0.3;
    }
  }
}

.services__item-text {
  padding: 24px 0 0 0;
  font-family: Open Sans, sans-serif;
  font-size: 19px;
  line-height: 24px;
  font-weight: 700;
  color: $white;
}

.services__item-icon {
  opacity: 0.15;
}

@media (min-width: $tablet-width) {

  .services {
    margin: 89px 0 0 0;

    &::after {
      content: "";
      width: 100%;
      height: 34px;
      position: absolute;
      left: 0;
      bottom: 0;
      background-image: url('../img/svg/bg-zigzag-fill.svg');
      background-repeat: no-repeat;
      background-position: bottom center;
    }
  }

  .services__container {
    padding: 153px 50px 14px 50px;
    position: relative;
    background-image: url('../img/content/hero-tablet@1x.jpg');
    background-position: 50% -1px;
    background-size: 1150px 746px;
  }

  @media (min-resolution: 144dpi),
         (min-resolution: 1.5dppx) {
    .services__container {
      background-image: url('../img/content/hero-tablet@2x.jpg');
    }
  }

  .services__header {
    margin-bottom: 373px;
    width: 100%;
    font-size: 43px;
    line-height: 50px;
    font-weight: 700;
  }

  .services__list {
    height: auto;
    display: flex;
  }
  
  .services__item {
    width: 50%;
    flex-grow: 1;
  }

  .services__item-btn {
    height: 117px;

    &--light {
      padding: 0 36px 0 36px;
    }

    &--dark {
      padding: 0 33px 0 36px;
    }
  }

  .services__item-text {
    padding: 27px 0 0;
    line-height: 30px;
  }

  .services__item-icon {
    padding: 5px 0 0 0;
  }
}

@media (min-width: $desktop-width) {

  .services {

    &::after {
      content: url('../img/svg/bg-zigzag-fill.svg');
      width: 1266px;
      height: 37px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: -20px;
      background-image: none;
    }
  }
  
  .services__container {
    padding: 153px 50px 0px 50px;
    background-image: url('../img/content/hero-desktop@1x.jpg');
  }

  @media (min-resolution: 144dpi),
         (min-resolution: 1.5dppx) {
    .services__container {
      background-image: url('../img/content/hero-desktop@2x.jpg');
    }
  }

  .services__header {
    margin-bottom: 360px;
    font-size: 53px;
    line-height: 57px;
    font-weight: 700;
  }

  .services__item-btn--dark {
    padding: 0 35px 0 36px;
  }

  .services__list {

    &::after {
      width: 100%;
      height: 37px;
      position: absolute;
      left: 0;
      bottom: -21px;
      background-size: cover;
    }
  }
}